<h1 id="android-services" tabindex="-1"><a class="header-anchor" href="#android-services" aria-hidden="true">#</a> Android Services</h1>
<p>So, you want your application to keep on running even if the user leaves your activities and start another application? Then tell android about it, and have your code continue to be executed by a service.</p>
